.Department {
	overflow: hidden;
	.about {
		width: 100%;
		margin-bottom: 150px;
		> .inner {
			&::after {
				content: '';
				display: block;
				clear: both;
			}
			width: 1180px;
			margin: 0 auto;
			position: relative;

			h1 {
				position: absolute;
				left: 34%;
				width: 100%;
				font: bold 50px/1 'Quicksand';
				color: rgb(114, 169, 107);
			}
			.aboutTxt {
				position: relative;
				width: 100%;
				margin-top: 30px;
				padding: 10px;
				h2 {
					position: absolute;
					top: 80px;
					left: 5%;
					font: bold 25px/1.4 'Quicksand';
					color: #333;
				}
				p {
					position: absolute;
					width: 60%;
					left: 34%;
					top: 80px;
					font: 20px/1.2 'Josefin Sans';
					letter-spacing: 1px;
					color: #333;
					span {
						font-weight: bold;
						color: rgb(114, 169, 107);
					}
				}
			}
			.aboutImg {
				width: 100%;
				height: 400px;
				margin-top: 300px;
				overflow: hidden;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 10px;
				}
			}
		}
	}
	.slogan {
		width: 100%;
		> .inner {
			width: 1180px;
			margin: 0 auto;
			position: relative;
			.sloganTxt {
				position: relative;
				width: 100%;
				margin-bottom: 150px;
				.fa-quote-left {
					font-size: 40px;
					color: #333;
					margin-bottom: 10px;
				}
				p {
					position: absolute;
					width: 37%;
					left: 4%;
					font: 38px/1.3 'Oleo Script';
					letter-spacing: 2px;
					color: #333;
				}
				.fa-quote-right {
					position: absolute;
					left: 43%;
					top: 280px;
					font-size: 40px;
					color: #333;
				}
			}
			.sloganImg {
				width: 48%;
				height: 310px;
				overflow: hidden;
				position: absolute;
				right: 0%;
				top: 0px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 10px;
				}
			}
		}
	}
	.team {
		width: 100%;
		> .inner {
			width: 1180px;
			height: 1100px;
			margin: 0 auto;
			position: relative;
			.teamImg {
				width: 65%;
				height: 800px;
				overflow: hidden;
				position: relative;
				top: 250px;

				img {
					width: 45%;
					height: 350px;
					object-fit: cover;
					margin-bottom: 20px;
					border-radius: 10px;
					filter: saturate(30%);
				}

				.team1 {
					transition: 0.5s;
					p {
						position: absolute;
						width: 120px;
						height: 90px;
						top: 16%;
						left: 38%;
						text-align: center;
						background: rgb(137, 199, 129);

						border-radius: 5px;
						padding: 10px;
						opacity: 0;
						transition: 0.5s;
						span {
							font: bold 30px/1.3 'Josefin Sans';
							color: rgb(255, 255, 255);
						}
						font: 20px/1 'Josefin Sans';
						color: #444;
						letter-spacing: 0px;
					}
					&:hover {
						img {
							filter: saturate(100%);
						}
						p {
							opacity: 1;
						}
					}
				}

				.team2 {
					position: absolute;
					width: 42%;
					left: 1%;
					transition: 0.5s;

					> img {
						width: 100%;
						height: 330px;
						border-radius: 10px;
						filter: saturate(30%);
					}
					p {
						position: absolute;
						width: 110px;
						height: 70px;
						top: 65%;
						left: 80%;
						text-align: center;
						background: rgb(137, 199, 129);
						border-radius: 5px;
						padding: 10px;
						opacity: 0;
						transition: 0.5s;
						span {
							font: bold 25px/1.3 'Josefin Sans';
							color: rgb(255, 255, 255);
						}
						font: 13px/1 'Josefin Sans';
						color: #444;
						letter-spacing: 0px;
					}
					&:hover {
						img {
							filter: saturate(100%);
						}
						p {
							opacity: 1;
						}
					}
				}

				.team3 {
					position: absolute;
					top: 30%;
					left: 49%;
					width: 38%;
					transition: 0.5s;
					> img {
						width: 100%;
						height: 320px;
						border-radius: 10px;
						filter: saturate(30%);
					}
					p {
						position: absolute;
						width: 100px;
						height: 70px;
						top: 55%;
						left: 83%;
						text-align: center;
						background: rgb(137, 199, 129);
						border-radius: 5px;
						padding: 10px;
						opacity: 0;
						transition: 0.5s;
						span {
							font: bold 25px/1.3 'Josefin Sans';
							color: rgb(255, 255, 255);
						}
						font: 11px/1 'Josefin Sans';
						color: #444;
						letter-spacing: 0px;
					}
					&:hover {
						img {
							filter: saturate(100%);
						}
						p {
							opacity: 1;
						}
					}
				}
			}

			.teamTxt {
				width: 35%;
				position: relative;
				top: -460px;
				left: 65%;
				h1 {
					font: bold 50px/1 'Quicksand';
					color: rgb(114, 169, 107);
					margin-bottom: 35px;
				}
				p {
					font: 25px/1.3 'Josefin Sans';
					color: #333;
				}
			}
		}
	}
}
